require(["highlight.pack"], function (hljs) {
    "use strict";

    onmessage = function (event) {
        var result = [];
        var len = event.data.length;
        for (var i = 0; i < len; i += 2) {
            var htmlContent = event.data[i];

            var langs = null
            if(event.data[i + 1].length) {
                langs = event.data[i + 1].split(" ");
            }
            var hasLanguage = langs !== null && langs.length;            
            var response;
            var success = false;

            if (hasLanguage)
            {
                try {
                    response = hljs.highlight(langs[0].replace('lang-',''), htmlContent, true); 
                    var responseValue = response.value;

                    result.push(responseValue);
                    success = true;
                }
                catch(err) {
                    //console.log('failure in Highlight.js Highlight, err:' + err );
                }
            }
            else {
                // no language specified, do not highlight
                result.push("--");
                success = true;
            }

            if (!success)
            {
                response = hljs.highlightAuto(htmlContent); 
                result.push(response.value);                
            }
        }
        postMessage(result);
    };
});


